body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 1280px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.NavBar {
	grid-column: 2 /3;
}

.NavBar ul {
	list-style-type: none;
	padding: 0;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 50px auto 0 auto;

	font-size: 40px;
}

.NavBar ul li {
	display: inline;
	margin-right: 30px;
}

.NavBar ul li {
	font-weight: bold;
	padding: 10px 20px 15px;
	border-radius: 44px;

	color: cornflowerblue;
}

.NavBar ul li:hover {
	background-color: cornflowerblue;

	color: white;
	cursor: pointer;
}

.NavBar ul li a {
	text-decoration: none;
	font-weight: bold;
	padding: 10px 20px 15px;
	border-radius: 44px;

	color: cornflowerblue;
}

.NavBar ul li a:hover {
	background-color: cornflowerblue;

	color: white;
}

.home {
	grid-column: 2 / 3;
	margin-top: 100px;

	display: grid;
	grid-template-columns: 2fr 3fr;
	-webkit-align-items: center;
	        align-items: center;
}

.home .home-content h1 {
	font-size: 7vw;
	margin-top: 0;
}

.home .home-content h2 {
	font-weight: lighter;
	margin-bottom: 0;
	/*font-size: 1.3vw;*/
}

.home .home-content .home-contact {
	margin-top: 50px;
}

.home-content .home-picture {
	margin-top: 50px;
}

.home > img {
	width: 100%;
}

@media all and (max-width: 1024px) {
	.home .home-content h1 {
		font-size: calc(70px + 2vw);
	}
}

.contact-icons a {
	margin-right: 20px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	background-color: inherit;
}

.contact-icons img {
	height: 50px;
}

.about-page {
	background-color: white;
	grid-column: 1/4;
	margin-top: 100px;
	display: grid;
	grid-template-columns: 1fr 5fr 1fr;

	padding-bottom: 100px;
}

.about {
	grid-column: 2 / 3;
}

.about > h1 {
	font-size: 60px;
	margin: 100px auto;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.blue-color {
	color: cornflowerblue;
}

.about-content {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-column-gap: 3vw;
}

.about-content > img {
	width: 100%;
	max-width: 560px;
	height: auto;
	-webkit-align-self: center;
	        align-self: center;
}

.github-resume {
	margin: 50px auto;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.github-resume a {
	padding: 10px 30px;
	margin-right: 100px;
	border-radius: 15px;
	box-shadow: 0 5px 0 grey;

	font-size: 17px;
	font-weight: bold;
	text-decoration: none;
	color: dodgerblue;
	background-color: #e5f4ff;
}

@media all and (max-width: 1024px) {
	.about-content {
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: auto auto;
	}

	.about-content img {
		justify-self: center;
		margin-bottom: 50px;
	}

	.github-resume a {
		font-size: 25px;
	}
}

.about-descriptions p {
	text-align: justify;
	font-size: 15px;
	display: block;
}

.about-descriptions p > a {
	text-decoration: none;
	font-weight: bold;
}

.about-descriptions h2 {
	font-size: 20px;
	margin-top: 40px;
}

.about-descriptions ul {
	list-style-type: none;

	padding: 0;
}

.about-descriptions ul li {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	display: inline-block;

	padding: 10px 20px 12px;
	margin-right: 10px;
	margin-bottom: 10px;
	background-color: #1f5673;
	border-radius: 5px;

	color: white;
}

@media all and (max-width: 1024px) {
	.about-descriptions p {
		font-size: 25px;
	}

	.about-descriptions ul li {
		font-size: 25px;
	}
}

.experiences {
	grid-column: 2 / 3;
	padding-bottom: 80px;
}

.experiences > h1 {
	font-size: 60px;
	margin: 100px auto;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.experience-card {
	padding: 2vw 5vw;
	margin-top: 80px;
	margin-bottom: 40px;

	border-radius: 20px;
	box-shadow: 1px 1px 15px 0px grey;

	background-color: white;
}

.experience-card .title-employer {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.experience-card .title-employer h1 {
	margin-right: 20px;
}

.experience-card .time-place {
	color: grey;
	font-weight: bold;
	font-size: 23px;

	border-bottom: 1px solid black;
}

.experience-card .time-place p {
	display: inline-block;
	margin-top: 0;
}

.experience-card .time-place .place {
	float: right;
}

.experience-card .detail-list {
	list-style-type: none;
	padding: 0 20px;
}

.experience-card .detail-list li {
	margin-top: 0px;
	font-size: 20px;
}

.detail-list li:before {
	content: "";
	border-color: transparent cornflowerblue;
	border-style: solid;
	border-width: 0.35em 0 0.35em 0.45em;
	display: block;
	height: 0;
	width: 0;

	left: -1em;
	top: 1.05em;
	position: relative;
}

.project-page {
	grid-column: 1/4;
	background-color: white;
	display: grid;
	grid-template-columns: 1fr 5fr 1fr;
}

.projects {
	grid-column: 2 / 3;

	padding-bottom: 100px;
}

.projects > h1 {
	font-size: 60px;
	margin: 100px auto;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.projects .filter-projects ul {
	list-style-type: none;

	padding: 0;
}

.projects .filter-projects ul li {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	display: inline-block;

	padding: 10px 20px 12px;
	margin-right: 10px;
	margin-bottom: 10px;
	background-color: #1f5673;
	border-radius: 5px;

	color: white;
}

.projects .filter-projects ul li:hover {
	cursor: pointer;
}

.projects .project-card {
	display: grid;
	grid-template-columns: 4fr 6fr;
	margin-top: 100px;
	box-shadow: 0 0px 50px 0 rgba(0, 0, 0, .16);
	border-radius: 20px;

	padding: 50px;
}

.projects .project-card .project-content {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;

	-webkit-justify-content: space-between;

	        justify-content: space-between;
}

.projects .project-card .project-content ul {
	list-style-type: none;

	padding: 0;
}

.projects .project-card .project-content ul li {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	display: inline-block;

	padding: 10px 20px 12px;
	margin-right: 10px;
	margin-bottom: 10px;
	background-color: #1f5673;
	border-radius: 5px;

	color: white;
}

.projects .project-card .project-content img {
	height: 50px;
	width: 50px;
	margin-right: 30px;
}

.projects .project-card .project-content .title {
	font-size: 50px;
	text-transform: uppercase;
	margin: 0;
}

.projects .project-card .project-content .descriptions {
	font-weight: normal;
	font-size: 20px;
	margin: 0;
}

.projects .project-card img {
	width: 100%;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, Omnes, Segoe UI, Roboto,
		Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue,
		sans-serif;
}

.body {
	background-color: #f9fafc;
	display: grid;
	grid-template-columns: 1fr 5fr 1fr;
}

.content {
	/* grid-column: 2 / 3;
	display: grid;
	grid-row-gap: 100px; */
}

.footer-page {
	background-color: #535353;
	grid-column: 1/ 4;

	display: grid;
	grid-template-columns: 1fr 5fr 1fr;
}

.footer {
	grid-column: 2/3;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.footer .copyright {
	margin-bottom: 15px;
}

.footer .copyright h1 {
	color: white;
	font-size: 50px;
	margin: 0;
}

.footer .copyright h2 {
	color: #ace8ff;
	margin: 0;
	font-size: 15px;
}

.footer .footer-contact {
	margin-top: auto;
	margin-bottom: auto;
}

